<script>
import { defineComponent, h, ref } from 'vue'
import { mixinColor, mixinView } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Stepper',
  mixins: [
    ...addMixinPrefix([
      mixinColor, mixinView()
    ], 'stepper')
  ],
  props: {
    steps: {
      type: Array
    },
    activeStep: {
      type: Number,
      default: 1
    }
  },
  setup(props, { slots }) { 
    const views = ref(['filled', 'smooth'])
    const step = ({number, label, isActive}) => {
      return h('div', { 
        class: ['step', isActive && 'step--active' || '']
      }, [
        h('div', {
          class: 'step__number'
        }, number),
        h('div', {
          class: 'step__label'
        }, label)
      ])
    }

    const steps = () => {
      const steps = []
      for( let [stepIndex, stepLabel] of Object.entries(props.steps) ){
        let stepNumber = Number(stepIndex) + 1
        let stepActive = stepNumber <= props.activeStep
        steps.push(step({number: stepNumber, label: stepLabel, isActive: stepActive}))
      }
      return steps
    }

    return {
      steps,
      views
    }
  },
  render() {
    const listClass = [
      'stepper',
      this.classColor,
      this.classView
    ]


    return h('div', {
      class: listClass
    }, this.steps())
  }
})
</script>